import { Component, Event, EventEmitter, h, Prop } from '@stencil/core';
import { NavigationThemeType } from '../../components';
import { RoutingKeys, RoutingKeyWithClick } from '../../entities/routing-key';
import { NavigationMode, NavigationModeType, NavigationTheme } from '../../utility/constants';
import { IsKeyboardClick } from '../../utility/helper';
import { breakpoint } from '@porsche-design-system/components-js/styles';
import pcomIcons from './pcom-level-0-icons';
import shopIcons from './shop-level-0-icons';
import { ApplicationType, Application, ContextualIconResponse } from '../../utility/constants/app';
import { burgerButtonSkeleton, pureButtonSkeleton } from '../../utility/skeleton';
import { isPcomSearchAvailable } from '../../utility/pcom-search-helper';
import { isShopAvailable } from '../../utility/helpers/shop-helper';
import { navContentStore } from '../../state/nav-content-store';

@Component({
  tag: 'phn-level-0-icons',
  styleUrl: 'level-0-icons.scss',
  scoped: true
})
export class Level0Icons {
  @Prop() locale!: string;
  @Prop() theme: NavigationThemeType = NavigationTheme.light;
  @Prop() mode: NavigationModeType = NavigationMode.navbar;
  @Prop() app: ApplicationType = Application.default;
  @Prop() breakpoint!: number;
  @Prop() isLoading!: boolean;
  @Prop() isNavigationLoaded!: boolean;

  @Event() openDrawer!: EventEmitter<RoutingKeyWithClick>;

  render(): HTMLPhnLevel0IconsElement {
    const contextualIconsProps = {
      locale: this.locale,
      app: this.app,
      mode: this.mode,
      theme: this.theme,
      isNavigationLoaded: this.isNavigationLoaded
    };

    const IconsStrategies: Record<string, () => ContextualIconResponse> = {
      pcom: () => pcomIcons(contextualIconsProps),
      shop: () => shopIcons(contextualIconsProps),
      default: () => ({ leftIcons: null, rightIcons: null })
    };

    const iconsStrategy = IconsStrategies[this.app];
    const contextualIcons = iconsStrategy();
    const hasMarketPorscheID = navContentStore.state.myPorsche;
    return (
      <div class="contextual-icons">
        <div class="left-icons">
          <phn-burger-button
            theme={this.theme}
            mode={this.mode}
            hideLabel={
              isPcomSearchAvailable(this.locale, this.app) || isShopAvailable(this.locale, this.app)
                ? this.breakpoint <= breakpoint.xs
                : this.breakpoint < breakpoint.xs
            }
            alignLabel={'right'}
            isLoading={this.isLoading}
            onClick={(e: MouseEvent) => {
              this.openDrawer.emit({ initialRoutingKey: '', isKeyboardClick: IsKeyboardClick(e) });
            }}
            style={this.isNavigationLoaded ? {} : { display: 'none' }}
          />
          {!this.isNavigationLoaded ? burgerButtonSkeleton() : null}

          {this.breakpoint <= breakpoint.xs ? contextualIcons.leftIcons : null}
        </div>

        <slot></slot>

        <div class="right-icons">
          {this.breakpoint > breakpoint.xs ? contextualIcons.leftIcons : null}
          {contextualIcons.rightIcons}

          {hasMarketPorscheID ? (
            <div class="icon">
              <phn-my-porsche-icon-button
                mode={this.mode}
                theme={this.theme}
                onClick={(e: MouseEvent) => {
                  this.openDrawer.emit({
                    initialRoutingKey: RoutingKeys.MY_PORSCHE,
                    isKeyboardClick: IsKeyboardClick(e)
                  });
                }}
                style={this.isNavigationLoaded ? {} : { display: 'none' }}
              />

              {!this.isNavigationLoaded ? pureButtonSkeleton() : null}
            </div>
          ) : null}
        </div>
      </div>
    );
  }
}
